import React, {Component} from 'react';

import Header from './Header';
import Body from './Body';
import Footer from './Footer';
import UserInfo from './UserInfo';

import '../styles/App.scss';

export default class App extends Component {
  
  state = {
    lostPets: [],
    lastIndex: 0,
    user: null,
    userDonations: []
  }

  componentDidMount() {
    fetch('./data-lostpets.json')
      .then(response => response.json())
      .then(result => {
        const pets = result.map(item => {
          this.setState({ lastIndex: item.petId + 1 })
          return item;
        })
        this.setState({
          lostPets: pets
        });
      });
  }

  signIn = (username, password) => {
    this.setState({ user: { username, password, } });
  }

  signOut = () => this.setState({ user: null });

  render() {
    let lostPets = this.state.lostPets;

    return (
      <div className="App">
        <Header 
          user={this.state.user} 
          onSignOut={this.signOut}
        />
        <UserInfo
          onSignIn={this.signIn}
          user={this.state.user}
          userDonations={this.state.userDonations} 
        />
        <Body lostPets={lostPets} />
        <Footer />  
      </div>
    );
  }
}


