import React, {Component} from 'react';

import Cards from './Cards';

export default class Body extends Component {
    static defaultProps = {
        lostPets: []
    }

    render() {


        return (
            <main className="App-body">
                <div className="wrapper">
                    <Cards lostPets={this.props.lostPets}/>
                </div>
            </main>
        );            
    }
}