import React, {Component} from 'react';

import '../styles/Cards.scss';

//import placeholder images
import img300 from '../images/300.jpg';
import img350 from '../images/350.jpg';
import img400 from '../images/400.jpg';
import img500 from '../images/500.jpg';
import img600 from '../images/600.jpg';
import img630 from '../images/630.jpg';
import img700 from '../images/700.jpg';


export default class Cards extends Component {
    static defaultProps = {
        lostPets: [],
    };

    render() {
        const listCards = this.props.lostPets.map(item => (
            <div className="card-item" key={item.petId}>
                <header>
                    <h2 className="title">
                        <span className="pet-name">{item.petName}</span> -- <span className="pet-id">#{item.petId}</span>
                    </h2>
                    <button className="btn small share">
                        Partilhar
                    </button>
                </header>
                <section className="content">
                    <div className="photo">
                        <img 
                            src = {(() => {
                                switch (item.photo) {
                                    case "300.jpg":   return img300;
                                    case "350.jpg":   return img350;
                                    case "400.jpg":   return img400;
                                    case "500.jpg":   return img500;
                                    case "600.jpg":   return img600;
                                    case "630.jpg":   return img630;
                                    default:      return img700;
                                }
                            })()}                            

                            alt="kitten" 
                        />
                    </div>
                    <div className="data">
                        <ul>
                            <li className="date-lost"><b>Perdido</b><span>Setembro 2018</span></li>
                            <li className="owner-name"><b>Done</b><span>Jorge Seabra</span></li>
                            <li className="locality"><b>Localidade</b><span>Faro</span></li>
                        </ul>
                    </div>
                    <div className="donate">
                        {/* <h3 className="amount">
                            <b>$50.000</b> doados
                        </h3> */}
                        <button className="btn donate-button">
                            Doar
                        </button>                
                    </div>
                </section>
                <footer>
                    <a href="tel:12345678" className="call-now">(Phone) Ligar</a>
                </footer>
            </div>
        ));
    
        return (
            <div className="cards-list">
                {listCards}
            </div>
        );            
    }
}