import React, {Component} from 'react';

import '../styles/Donations.scss';
// import logoIcon from '../images/logo.svg';
// import userIcon from '../images/user.svg';

const Welcome = ({ user, onSignOut }) => (
    <div className="Welcome">
        Welcome <strong>{user.username}</strong>!
        
    </div>
)

export default class Donations extends Component {
    static defaultProps = {
        user: {},
        userDonations: {},
    }

    render() {

        return (
            <div className="Donations">
                <div className="wrapper">
                    <div className="row">
                        <div className="col donations-list text-center">
                            <Welcome
                                user={this.props.user}
                            />
                            <h4>Your Donations so far:</h4>
                            <div className="donation-item">
                                <span className="donation-name">TO: Bruno</span> -
                                <span className="donation-amount">$9</span>
                            </div>
                            <div className="donation-item">
                                <span className="donation-name">TO: Mike Myers</span> -
                                <span className="donation-amount">$9</span>
                            </div>
                            <div className="donation-item">
                                <span className="donation-name">TO: A dog in Glasgow</span> -
                                <span className="donation-amount">$3</span>
                            </div>
                            <div className="donation-item">
                                <span className="donation-name">TO: Anonymous</span> -
                                <span className="donation-amount">$9</span>
                            </div>
                        </div>
                    </div>                    
                </div>
            </div>
        );            
    }
}